import React, { useEffect, useState } from 'react';
import Layout from '../components/Layout/Layout';
import Button from '../components/Button/Button';
import { useUser } from '../context/UserContext';
import * as styles from '../styles/dashboard.module.css';

const DashboardPage: React.FC = () => {
  // We strictly only render after mount to avoid hydration mismatch with localStorage
  const [mounted, setMounted] = useState(false);
  const { user, clearData } = useUser();

  useEffect(() => {
    setMounted(true);
  }, []);

  if (!mounted) {
    return (
      <Layout>
        <div style={{ padding: `2rem`, textAlign: `center` }}>
          Loading your profile...
        </div>
      </Layout>
    );
  }

  const handleClear = () => {
    if (
      window.confirm(
        `Are you sure you want to delete all specific progress data? This cannot be undone.`,
      )
    ) {
      clearData();
    }
  };

  return (
    <Layout>
      <div className={styles.dashboardHeader}>
        <h1>Learner Dashboard</h1>
        <p>Track your etymology journey.</p>
      </div>

      <div className={styles.statsGrid}>
        <div className={styles.statCard}>
          <div className={styles.statValue}>üî• {user.streak}</div>
          <div className={styles.statLabel}>Day Streak</div>
        </div>
        <div className={styles.statCard}>
          <div className={styles.statValue}>{user.wordsMastered}</div>
          <div className={styles.statLabel}>Words Mastered</div>
        </div>
        <div className={styles.statCard}>
          <div className={styles.statValue}>{user.labsCompleted}</div>
          <div className={styles.statLabel}>Labs Completed</div>
        </div>
      </div>

      <div className={styles.section}>
        <h3>Recent Activity</h3>
        <p
          style={{ marginBottom: `1rem`, color: `var(--color-text-secondary)` }}
        >
          Last played:{` `}
          {user.lastPlayed
            ? new Date(user.lastPlayed).toLocaleDateString()
            : `Never`}
        </p>

        <div className={styles.progressContainer}>
          <h4>Accuracy Trend (Last 5 Sessions)</h4>
          {/* Mock chart for visual demonstration as we don't store session history array yet */}
          <div className={styles.barChart}>
            <div
              className={styles.bar}
              style={{ height: `40%` }}
              title="Session 1: 40%"
            ></div>
            <div
              className={styles.bar}
              style={{ height: `60%` }}
              title="Session 2: 60%"
            ></div>
            <div
              className={styles.bar}
              style={{ height: `75%` }}
              title="Session 3: 75%"
            ></div>
            <div
              className={styles.bar}
              style={{ height: `50%` }}
              title="Session 4: 50%"
            ></div>
            <div
              className={styles.bar}
              style={{ height: `90%` }}
              title="Session 5: 90%"
            ></div>
          </div>
          <p
            style={{
              fontSize: `0.8rem`,
              textAlign: `center`,
              marginTop: `0.5rem`,
              color: `#666`,
            }}
          >
            (Chart data is illustrative in this version)
          </p>
        </div>
      </div>

      <div className={styles.dangerZone}>
        <h3>Data Management</h3>
        <p style={{ marginBottom: `1rem` }}>
          Your progress is stored locally on this device.
        </p>
        <Button variant="danger" onClick={handleClear}>
          Reset All Progress
        </Button>
      </div>
    </Layout>
  );
};

export default DashboardPage;
